import { ItcSimpleSlider } from './simple-adaptive-slider'

document.addEventListener('DOMContentLoaded', () => {
    // инициализация слайдера
    new ItcSimpleSlider('.itcss', {
      loop: true,
      autoplay: false,
      interval: 5000,
      swipe: true,
    });

    // удаляем индикаторы
    let indicators = document.querySelector('.itcss__indicators')
    
    if(indicators){
      indicators.remove()
    }
  });

